// components/ModalNovoAluno.tsx

import React, { useState } from 'react';
import { supabase } from '../lib/supabaseClient';
import { Loader2 } from 'lucide-react';

interface ModalNovoAlunoProps {
  onClose: () => void;
  onAlunoAdicionado: () => void; // Callback para atualizar a lista
}

const ModalNovoAluno: React.FC<ModalNovoAlunoProps> = ({ onClose, onAlunoAdicionado }) => {
  const [nome, setNome] = useState('');
  const [nomePai, setNomePai] = useState('');
  const [emailPai, setEmailPai] = useState('');
  const [valor, setValor] = useState<number | ''>(''); // Usar '' para input vazio
  const [vencimento, setVencimento] = useState<string>(new Date().toISOString().slice(0, 10)); // Formato YYYY-MM-DD
  const [loadingModal, setLoadingModal] = useState(false);
  const [errorModal, setErrorModal] = useState<string | null>(null);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoadingModal(true);
    setErrorModal(null);

    // --- Validação Simples ---
    if (!nome || !emailPai || valor === '' || !vencimento) {
      setErrorModal('Preencha todos os campos obrigatórios.');
      setLoadingModal(false);
      return;
    }
    if (valor < 0) {
       setErrorModal('O valor da mensalidade não pode ser negativo.');
       setLoadingModal(false);
       return;
    }

    // --- Verificação de Limite de Alunos ---
    const { data: { user } } = await supabase.auth.getUser();
    if (!user) {
      setErrorModal('Usuário não autenticado. Faça login novamente.');
      setLoadingModal(false);
      return;
    }

    try {
        // 1. Busca dados do treinador (plano e limite)
        const { data: treinadorData, error: treinadorError } = await supabase
            .from('treinadores')
            .select('plano_atual, limite_alunos')
            .eq('id', user.id)
            .single();

        if (treinadorError) throw new Error("Erro ao buscar dados do plano.");

        // 2. Conta alunos atuais
        const { count: alunosAtuais, error: countError } = await supabase
            .from('alunos')
            .select('*', { count: 'exact', head: true })
            .eq('id_treinador', user.id);

        if (countError) throw new Error("Erro ao contar alunos atuais.");

        // 3. Verifica o Limite (Considera Premium como Ilimitado)
        if (treinadorData.plano_atual !== 'premium' && (alunosAtuais ?? 0) >= treinadorData.limite_alunos) {
             setErrorModal(`Limite de ${treinadorData.limite_alunos} alunos atingido para o plano ${treinadorData.plano_atual.toUpperCase()}. Faça upgrade para adicionar mais.`);
             setLoadingModal(false);
             return;
        }

        // --- Inserção do Aluno ---
        const payload = {
          nome_aluno: nome,
          nome_pai: nomePai || 'Não informado', // Valor padrão se vazio
          email_pai: emailPai,
          data_matricula: new Date().toISOString().slice(0, 10), // Apenas data
          // Garante que a data de vencimento esteja no formato correto e UTC
          data_vencimento_mensalidade: new Date(vencimento + 'T00:00:00Z').toISOString().slice(0, 10), 
          status_mensalidade: 'paga' as const, // Assume pago na matrícula
          valor_mensalidade: valor,
          id_treinador: user.id
        };

        const { error: insertError } = await supabase.from('alunos').insert([payload]);

        if (insertError) {
          console.error('Erro ao inserir aluno:', insertError);
          throw new Error('Erro ao cadastrar atleta. Verifique os dados e tente novamente.');
        }

        // Sucesso!
        setLoadingModal(false);
        onAlunoAdicionado(); // Chama o callback para fechar e recarregar a lista

    } catch (err: any) {
        console.error("Erro no Modal:", err);
        setErrorModal(err.message || 'Ocorreu um erro inesperado.');
        setLoadingModal(false);
    }
  };

  return (
    // Overlay e Centralização
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-70 backdrop-blur-sm">
      {/* Contêiner do Modal */}
      <div className="bg-gray-800 p-8 rounded-xl shadow-2xl z-10 w-full max-w-lg border border-gray-700">
        <h2 className="text-2xl font-bold mb-6 text-white text-center">Adicionar Novo Atleta</h2>
        
        {/* Mensagem de Erro */}
        {errorModal && <p className="text-red-400 bg-red-900/50 p-3 rounded mb-4 text-center">{errorModal}</p>}
        
        {/* Formulário */}
        <form onSubmit={handleSubmit} className="space-y-4">
          {/* Nome do Aluno */}
          <div>
            <label htmlFor="nome-aluno" className="block text-sm font-medium text-gray-300 mb-1">Nome do Atleta*</label>
            <input
              id="nome-aluno"
              value={nome}
              onChange={(e) => setNome(e.target.value)}
              required
              className="w-full px-4 py-2 rounded-lg bg-gray-700 text-white border border-gray-600 focus:outline-none focus:ring-2 focus:ring-dribla-green focus:border-transparent"
              placeholder="Nome completo do aluno"
            />
          </div>

          {/* Nome do Responsável */}
          <div>
            <label htmlFor="nome-pai" className="block text-sm font-medium text-gray-300 mb-1">Nome do Responsável</label>
            <input
              id="nome-pai"
              value={nomePai}
              onChange={(e) => setNomePai(e.target.value)}
              className="w-full px-4 py-2 rounded-lg bg-gray-700 text-white border border-gray-600 focus:outline-none focus:ring-2 focus:ring-dribla-green focus:border-transparent"
              placeholder="(Opcional)"
            />
          </div>

          {/* Email do Responsável */}
          <div>
            <label htmlFor="email-pai" className="block text-sm font-medium text-gray-300 mb-1">Email do Responsável*</label>
            <input
              id="email-pai"
              value={emailPai}
              onChange={(e) => setEmailPai(e.target.value)}
              type="email"
              required
              className="w-full px-4 py-2 rounded-lg bg-gray-700 text-white border border-gray-600 focus:outline-none focus:ring-2 focus:ring-dribla-green focus:border-transparent"
              placeholder="Para envio de cobranças"
            />
          </div>

          {/* Linha para Valor e Vencimento */}
          <div className="flex flex-col sm:flex-row space-y-4 sm:space-y-0 sm:space-x-4">
            {/* Valor da Mensalidade */}
            <div className="flex-1">
              <label htmlFor="valor" className="block text-sm font-medium text-gray-300 mb-1">Valor Mensalidade (R$)*</label>
              <input
                id="valor"
                value={valor}
                onChange={(e) => setValor(e.target.value === '' ? '' : Number(e.target.value))}
                type="number"
                min="0"
                step="0.01"
                required
                className="w-full px-4 py-2 rounded-lg bg-gray-700 text-white border border-gray-600 focus:outline-none focus:ring-2 focus:ring-dribla-green focus:border-transparent"
                placeholder="Ex: 100.00"
              />
            </div>
            {/* Data de Vencimento */}
            <div className="flex-1">
              <label htmlFor="vencimento" className="block text-sm font-medium text-gray-300 mb-1">Próximo Vencimento*</label>
              <input
                id="vencimento"
                value={vencimento}
                onChange={(e) => setVencimento(e.target.value)}
                type="date"
                required
                className="w-full px-4 py-2 rounded-lg bg-gray-700 text-white border border-gray-600 focus:outline-none focus:ring-2 focus:ring-dribla-green focus:border-transparent"
              />
            </div>
          </div>

          {/* Botões de Ação */}
          <div className="flex justify-end space-x-3 pt-4">
            <button 
              type="button" 
              onClick={onClose} 
              className="px-5 py-2 rounded-lg bg-gray-600 text-white hover:bg-gray-500 transition duration-200"
            >
              Cancelar
            </button>
            <button
              type="submit"
              disabled={loadingModal}
              className={`px-5 py-2 rounded-lg bg-dribla-green text-gray-900 font-semibold hover:bg-green-600 transition duration-200 flex items-center ${loadingModal ? 'opacity-70 cursor-not-allowed' : ''}`}
            >
              {loadingModal ? <Loader2 className="w-5 h-5 animate-spin mr-2" /> : null}
              {loadingModal ? 'Salvando...' : 'Adicionar Atleta'}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default ModalNovoAluno;

